import React, { useEffect, useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { useSearchParams } from "react-router-dom";
import { addItem, removeItem } from "../../../features/AddToCart/cartSlice";
import Footer from "../Footer";
import NavigationBar from "../NavgationBar";
import Header from "../Header";

import classes from "../PopularItems/index.module.css";

export default function Search() {
  const [searchParams] = useSearchParams();
  const [items, setItems] = useState([]);
  const dispatch = useDispatch();

  const addToCart = (item) => {
    dispatch(addItem(item));
  };

  const removeFromCart = (item) => {
    dispatch(removeItem(item));
  };

  const itemsInCart = useSelector((state) => state.cart.cartItems);

  const renderButton = (item) => {
    let value = 0;
    itemsInCart.forEach((foodItem) => {
      if (foodItem.id === item.id) {
        value = foodItem.cartQuantity;
      }
    });
    return (
      <>
        {value === 0 ? (
          <span className={`${classes["itemQuantityText"]}`}>Add</span>
        ) : (
          <span className={`${classes["itemQuantityText"]}`}>{value}</span>
        )}
      </>
    );
  };

  useEffect(() => {
    const fetchItems = async () => {
      const api = await fetch(
        `https://api.spoonacular.com/recipes/complexSearch?query=${searchParams.get(
          "name"
        )}&addRecipeInformation=true&apiKey=${
          process.env.REACT_APP_API_KEY
        }&number=10`
      );

      const data = await api.json();

      setItems(data.results);
    };

    fetchItems();
    // eslint-disable-next-line
  }, [searchParams.get("name")]);

  const errorMsg = (
    <h4 className="fw-bold text-center col-12 fs-2 text-danger">
      Please Search Again!
    </h4>
  );
  return (
    <>
      <NavigationBar />
      <Header />
      <Container>
        <Row className="mt-5 mb-5">
          <Col xs={12}>
            {items.length === 0 ? (
              errorMsg
            ) : (
              <>
                <h2
                  className={`${classes["popular-items-title"]} fw-bold mb-3`}
                >
                  Here You Go Your Favourite Foodddd!
                </h2>
                <div className={classes["grid-container"]}>
                  {items.map((item) => (
                    <div
                      className={`${classes["grid-item"]} ${classes["gridCards"]} shadow`}
                      key={item.id}
                    >
                      <div className={classes["card"]}>
                        <div className={classes["card-image-container"]}>
                          <img
                            className="card-img-top"
                            src={item.image}
                            alt={item.title}
                          />
                        </div>

                        <div className="card-body d-flex flex-column justify-content-between p-3">
                          <h5 className={classes["card-title"]}>
                            {item.title}
                          </h5>
                          <div className="d-flex align-items-center justify-content-between">
                            <p className={classes["card-text"]}>
                              {" "}
                              price: ${item.pricePerServing}{" "}
                            </p>
                            <div
                              className="btn-group w-50"
                              role="group"
                              aria-label="..."
                            >
                              <button
                                type="button"
                                className="btn btn-primary"
                                onClick={() => removeFromCart(item)}
                              >
                                -
                              </button>
                              <button type="button" className="btn btn-light">
                                {renderButton(item)}
                              </button>

                              <button
                                type="button"
                                className="btn btn-primary"
                                onClick={() => addToCart(item)}
                              >
                                +
                              </button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </>
            )}
          </Col>
        </Row>
      </Container>
      <Footer />
    </>
  );
}
