import { Col, Row } from "react-bootstrap";
import { BiChevronRight } from "react-icons/bi";
import data from "../../Constants/data";
import classes from "./index.module.css";

const PopularLocalities = () => {
  return (
    <Row className={classes["popular-locality-section"]}>
      <Col xs={12}>
        <h2 className={classes["popular-locality-title"]}>
          Popular localities in and around
          <span className={classes["popular-sub-title"]}> Mumbai</span>
        </h2>
      </Col>
      <Row>
        {data.localitiesData.map((locality) => (
          <Col
            lg={4}
            md={6}
            sm={6}
            xs={12}
            className="mb-3 border-0"
            key={locality.city}
          >
            <div
              className={`${classes["popular-locality-container"]} d-flex justify-content-between align-items-center`}
            >
              <div>
                <p className={classes["popular-locality-name"]}>
                  {locality.city}
                </p>
                <p className={classes["popular-locality-places"]}>
                  {locality.num} places
                </p>
              </div>
              <BiChevronRight
                className={classes["popular-locality-right-icon"]}
              />
            </div>
          </Col>
        ))}
        <Col lg={4} md={6} sm={6} xs={12} className="mb-3">
          <div className={classes["popular-locality-container"]}>
            <div
              className={`${classes["popular-locality-see-more-container"]} d-flex justify-content-center align-items-center`}
            >
              <p className={classes["popular-locality-see-more-text"]}>
                see more
              </p>
              <BiChevronRight
                className={`${classes["popular-locality-right-icon"]} ${classes["popular-locality-rotated-icon"]}`}
              />
            </div>
          </div>
        </Col>
      </Row>
    </Row>
  );
};

export default PopularLocalities;
